.header {
  padding: 10px var(--horizontalPadding);
}

.header {
  height: 50px;
  width: 100%;
  background-color: var(--primary);
  transition: all 0.3s;
}

.header:hover {
  background-color: var(--hover);
}

.block {
  display: flex;
  align-items: center;
  justify-content: left;
  height: 100%;
}
.logo {
  height: 100%;
}
.logo img {
  height: 100%;
}
.title {
  color: var(--black);
  font-size: 20px;
  font-weight: bold;
  letter-spacing: 1px;
  padding-left: 30px;
}
